<template>
    <div>
        <b-modal id="deactiveUser" hide-footer hide-header centered>
            <template v-slot:modal-title>
                <h5 class="modal-title">User Management</h5>
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="userStatus" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you Sure</h3>
                    <p v-if="userStatus" class="mb-5">you want to activate this User?</p>
                    <p v-else class="mb-5">you want to deactivate this User?</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactiveUser')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" v-on:click="activateDeactivateUser();">Ok</button>
                </div>
            </div>
        </b-modal>
        <div class="d-block header-section">
            <div class="py-3 px-3 align-self-center d-sm-flex w-100 rounded">
                <div class="w-sm-100 mr-auto"><h1><b>User Management</b></h1></div>
                <div class="float-right">
                    <button class="btn btn-link float-right" v-on:click="loadNewUser()" type="button">
                        <img src="@/assets/images/icons/add.png" />
                        <span class="text-primary">
                            <b>NEW USER</b>
                        </span>
                    </button>
                </div>
            </div>
        </div>
        <div class="container-fluid productManagement p-0">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 col-md-12 mt-3 mb-5">
                    <div class="card">
                        <div class="card-header p-0">
                            <b-collapse id="user" v-model="showNewUser">
                                <div class="col-12 col-sm-12">
                                    <div class="form-row ml-3 mb-4 mt-5">
                                        <div class="form-group row col-md-6" :class="{'form-group-error': $v.objUser.name.$error,}">
                                            <label for="username" class="col-sm-3 col-form-label">Name</label>
                                            <div class="col-sm-8">
                                                <b-form-input type="text" class="form-control" v-model="objUser.name" placeholder="Name" />
                                                <div v-if="userError">
                                                    <span class="text-danger" v-if="!$v.objUser.name.required">Name is Required</span>
                                                    <span class="text-danger" v-if="!$v.objUser.name.checkUserNameDuplicate">Name already exist</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6" :class="{'form-group-error': $v.objUser.email.$error,}">
                                            <label for="username" class="col-sm-3 col-form-label">Email Id</label>
                                            <div class="col-sm-8">
                                                <b-form-input type="text" class="form-control" v-model="objUser.email" placeholder="Email Id" />
                                                <div v-if="userError">
                                                    <span class="text-danger" v-if="!$v.objUser.email.required">Email Id is Required</span>
                                                    <span class="text-danger" v-else-if="!$v.objUser.email.email">Email id is not valid</span>
                                                    <span class="text-danger" v-else-if="!$v.objUser.email.checkEmailDuplicate">Email id already exist</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div class="form-row ml-3 mb-4">
                                        <div class="form-group row col-md-6">
                                            <label for="username" class="col-sm-3 col-form-label">Role</label>
                                            <div class="col-sm-8" :class="{'form-group-error': $v.objUser.role.$error}">
                                                <b-form-select v-model="objUser.role">
                                                    <b-form-select-option value="">Select Roles</b-form-select-option>
                                                    <b-form-select-option v-for="(role,index) in this.$store.state.roles" :key="index" :value="role.enumName">{{ role.enumDisplayName }}</b-form-select-option>
                                                </b-form-select>
                                                <div v-if="userError">
                                                    <span class="text-danger" v-if="!$v.objUser.role.required">Role is required</span>
                                                    <span class="text-danger" v-else-if="!$v.objUser.role.checkNullOrEmpty">Select a Role</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group row col-md-6" v-if="currentOrgType!=='TaxAuthority' && currentOrgType!=='Shipper' && currentOrgType!=='Ntl'">
                                            <label for="username" class="col-sm-3 col-form-label">Location</label>
                                            <div class="col-sm-8" :class="{'form-group-error': $v.objUser.location.$error}">
                                                <b-form-select v-model="objUser.location">
                                                    <b-form-select-option value="">Select Location</b-form-select-option>
                                                    <b-form-select-option v-for="(warehouse,index) in $store.getters.warehouses" :key="index" :value="warehouse.name">{{ warehouse.name }}</b-form-select-option>
                                                </b-form-select>
                                                <div v-if="userError">
                                                    <span class="text-danger" v-if="!$v.objUser.location.checkValid">Location is required</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-row ml-3">
                                        <div class="form-group row col-md-6" :class="{'form-group-error': $v.objUser.phone.$error}">
                                            <label for="username" class="col-sm-3 col-form-label">Contact Number</label>
                                            <div class="col-sm-8">
                                                <div class="input-group mb-3">
                                                    <div class="form-group col-sm-4 p-0" :class="{ 'form-group--error': $v.objUser.countryCode.$error }">
                                                        <b-form-input type="number" class="form-control " placeholder="+91" v-model="objUser.countryCode" />
                                                        <div v-if="userError">
                                                            <span class="text-danger" v-if="!$v.objUser.countryCode.required">Country code is required</span>
                                                        </div>
                                                    </div>
                                                    <div class="form-group col-sm-8 pr-0" :class="{ 'form-group--error': $v.objUser.phone.$error }">
                                                        <b-form-input type="text" class="form-control" v-model="objUser.phone" placeholder="Contact Number" />
                                                        <p v-if="userError"> <span class="text-danger" v-if="!$v.objUser.phone.required">Contact Number is required</span></p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <button class="btn btn-primary float-right mb-4 pl-5 pr-5 p-3" name="save" value="SAVE" type="button" v-on:click="addUpdateUser()">Save</button>
                                    <button class="btn btn-outline-primary float-right mb-4 pl-5 pr-5 mr-3 p-3" v-on:click="cancel()" name="save" value="CANCEL" type="button">CANCEL</button>
                                </div>
                                <div class="clearfix"></div>
                            </b-collapse>
                            <div class="list-filter">
                                <div class="btn-group">
                                    <b-form-radio id="All" name="radios" autocomplete="off" class="custom-control-input" checked :value="allUsersCount" />
                                    <label for="All" type="button" class="btn btn-outline-dark rounded-btn mr-2" @click="onStatusFilterClicked('All')">ALL({{ allUsersCount }})</label>
                                </div>
                                <div class="btn-group">
                                    <b-form-radio name="radios" autocomplete="off" class="custom-control-input" id="Active" :value="activeUsersCount" />
                                    <label type="button" class="btn btn-outline-warning rounded-btn mr-2 mob-mar" for="Active" @click="onStatusFilterClicked('Active')">Active({{activeUsersCount}})</label>
                                </div>
                                <div class="btn-group">
                                    <b-form-radio name="radios" autocomplete="off" class="custom-control-input" id="InActive" :value="activeUsersCount" />
                                    <label type="button" class="btn btn-outline-primary rounded-btn mr-2 mob-mar" for="InActive" @click="onStatusFilterClicked('InActive')">InActive({{ InactiveUsersCount }})</label>
                                </div>
                            </div>
                            <div class="col-12 col-sm-12 p-0">
                                <div class="table-responsive">
                                    <div class="search-tbl">
                                        <div class="form-row col-md-12">
                                            <div class="col-md-3 mt-3">
                                                <b-form-select v-model="selectedRole" class="mb-3" v-on:change="gridRefresh">
                                                    <b-form-select-option value="">Select Roles</b-form-select-option>
                                                    <b-form-select-option v-for="(role,index) in this.$store.state.roles" :key="index" :value="role.enumName">{{ role.enumDisplayName }}</b-form-select-option>
                                                </b-form-select>
                                            </div>
                                            <div class="col-md-3 mt-3">
                                                <b-input-group>
                                                    <b-form-input v-model="searchPattern" class="form-control" type="search" id="filterInput" placeholder="search- Name, Email"></b-form-input>
                                                    <b-input-group-append>
                                                        <b-button :disabled="!searchPattern" @click="searchPattern = ''">Clear</b-button>
                                                    </b-input-group-append>
                                                </b-input-group>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="overflow-auto">
                                        <b-table id="userList"
                                                 :items="this.getUsers"
                                                 :filter="searchPattern"
                                                 filter-debounce="1000"
                                                 :fields="dataFields"
                                                 :current-page="currentPage"
                                                 :per-page="perPage"
                                                 class="table order">
                                                 <template v-slot:cell(isActive)="data">
				<span :class="data.item.status==='Active'?'text-success':'text-danger'">{{data.item.status}}</span>
				</template>
                                            <template v-slot:cell(action)="data">
                                                <b-button variant="link" class="text-primary pr-2" v-on:click="editUser(data.item.id)"><img src="@/assets/images/icons/edit-gray.png" /> Edit</b-button>
                                                <b-button variant="link" v-if="data.item.isActive==='Active'" v-on:click="getactiveDeactiveStatus(data.item.id,false)"><img src="@/assets/images/icons/activate.png" />Deactivate</b-button>
                                                <b-button variant="link" v-else v-on:click="getactiveDeactiveStatus(data.item.id,true)"><img src="@/assets/images/icons/deactivate-new.png" /> Activate</b-button>
                                            </template>
                                        </b-table>
                                        <b-pagination v-model="currentPage" aria-controls="userList" :total-rows="this.totalRows" :per-page="perPage" class="float-right mr-4" first-text="First" prev-text="Prev" next-text="Next" last-text="Last">
                                        </b-pagination>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- END: Card DATA-->
        </div>
    </div>
</template>

<script>
    import { required, email } from "vuelidate/lib/validators";
    import { checkNullOrEmpty, checkEmailDuplicate,checkUserNameDuplicate } from '@/Validations';
    import toast from '@/toast';
    import axios from 'axios';
    export default {
        data() {
            return {
                //grid fields
                searchPattern: '', totalRows: 1, currentPage: 1, perPage: 5, pageOptions: [5, 10, 15],
                dataFields: [{ key: 'userName', label: 'Name', sortable: true }, { key: 'role', label: 'Role', sortable: true },
                { key: 'email', label: 'Email', sortable: true }, { key: 'isActive', label: 'Status', sortable: true },
                { key: 'action', label: 'Action', sortable: true }],objUser: this.assignUserFields(),showNewUser:false,
                userError: false,allUsersCount: 0,activeUsersCount: 0,InactiveUsersCount: 0,filterStatus: 'All',
                selectedStatus: '',selectedRole: '',currentOrgId: 0,currentOrgType: '',currentOrgName: '', userId:'', userStatus:true
            };
        },
        validations: {
            objUser: {
                name: { required,
                checkUserNameDuplicate(value) {
                    if (this.objUser.id !== 0) return true;
                        return checkUserNameDuplicate(value);
                } },
                email: { required, email,
                checkEmailDuplicate(value) {
                    if (this.objUser.id !== 0) return true;
                        return checkEmailDuplicate(value);
                } },
                role: { required,checkNullOrEmpty },
                location: { checkValid(value) { if (value == "" && this.currentOrgType !== 'Ntl'&& this.currentOrgType !== 'TaxAuthority' && this.currentOrgType !== 'Shipper') return false; return true; } },
                phone: { required }, countryCode: { required }
            }
        },
        async mounted() {
            if (this.shipperId && this.shipperName && this.shipperId !== undefined && this.shipperName !== undefined) {
                this.currentOrgId = this.shipperId; this.currentOrgName = this.shipperName; this.currentOrgType = 'Shipper'
            }
            else {
                this.currentOrgId = this.$store.state.clientAppDecodedToken.OrgId; this.currentOrgType = this.$store.state.clientAppDecodedToken.OrgType; this.currentOrgName = this.$store.state.clientAppDecodedToken.OrgName;
            }
            await this.$store.dispatch("getRolesAsync", this.currentOrgType);
            if(this.currentOrgType !== 'TaxAuthority' && this.currentOrgType !== 'Shipper'&& this.currentOrgType !== 'Ntl')
                this.$store.dispatch("getOrgWarehousesAsync", this.currentOrgId);
        },
        props:["shipperId","shipperName"],
        methods: {
            assignUserFields() {
                var obj = { id: 0, name: '', email: '', role: '', location: '', phone: '', countryCode: '+91' };
                return obj;
            },
            getUsers(ctx) {
                const promise = axios({
                    url: '/User/GetUsers',
                    method: 'post',
                    baseURL: process.env.VUE_APP_UmApisBaseUrl,
                    data: {
                        PageSize: ctx.perPage, PageNumber: ctx.currentPage,
                        SearchByParameter: this.searchPattern, SearchByStatus: this.filterStatus,
                        OrgName: this.currentOrgName, UserRole: this.selectedRole
                    }
                })
                return promise.then(response => {
                    const items = response.data.miniUserDtos;
                    this.totalRows=response.data.totalCount;
                    this.allUsersCount = response.data.totalCount;
                    this.activeUsersCount = response.data.activeCount;
                    this.InactiveUsersCount = response.data.inActiveCount;
                    items.forEach(element => {
                        element.isActive = element.isActive == true ? 'Active' : 'Deactive';
                        element.role = this.assigningRole(element.role);
                    });
                    return items || [];
                })
            },
            onStatusFilterClicked(status) {
                this.filterStatus = status;
                this.gridRefresh();
            },
            gridRefresh() {
                this.$root.$emit('bv::refresh::table', 'userList');
            },
            assigningRole: function (value) {
                if (value === null || value === "") return "";
                var role = this.$store.getters.roles.filter(element => element.enumName === value).map(element => element.enumDisplayName);
                return role !== [] ? role[0] : [];
            },
            loadNewUser() {
                this.objUser = this.assignUserFields();this.showNewUser=true;
            },
            async addUpdateUser() {
                this.$v.$touch();
                this.userError = this.$v.objUser.$anyError;
                if (!this.userError) {
                    var location = (this.currentOrgType !== 'TaxAuthority' && this.currentOrgType !== 'Shipper'&& this.currentOrgType !== 'Ntl') ? this.$store.state.warehouses.filter(element => element.name === this.objUser.location) : null;
                    var locationName = location != null ? location[0].name : null;
                    var locationId = location != null ? location[0].id : null;
                    var objAddUser = { email: this.objUser.email, userName: this.objUser.email, phone: this.objUser.phone, countryCode: this.objUser.countryCode, id: this.objUser.id };
                    var objAddClaims = {
                        GivenName: this.objUser.name, Role: this.objUser.role, Location: this.objUser.location,
                        OrgName: this.currentOrgName, OrgType: this.currentOrgType, OrgId: this.currentOrgId, LocationName: locationName, LocationId: locationId
                    };
                    objAddUser.claims = objAddClaims;
                    const promise = axios({
                        url: this.objUser.id === 0 ? "/User" : "/User/UpdateUser",
                        method: 'post',
                        baseURL: process.env.VUE_APP_UmApisBaseUrl,
                        data: objAddUser
                    })
                    promise.then(response => {
                        if (response.status == 200){
                            this.gridRefresh();
                            toast.success("User Saved!..");
                        }
                    })
                    this.gridRefresh(); this.objUser = this.assignUserFields();
                    this.showNewUser=false;
                }
            },
            editUser(userId) {
                const promise = axios({
                    url: "/User?userId=" + userId,
                    method: 'get',
                    baseURL: process.env.VUE_APP_UmApisBaseUrl,
                })
                promise.then(response => {
                    if (response.status == 200) {
                        var currentUser = response.data;
                        this.objUser = { id: currentUser.id, name: '', email: currentUser.email, role: '', location: '', phone: currentUser.phoneNumber, countryCode: currentUser.countryCode };
                        this.objUser.name = currentUser.claims.filter(element => element.claimType === 'GivenName').map(element => element.claimValue).toString();
                        this.objUser.role = currentUser.claims.filter(element => element.claimType === 'Role').map(element => element.claimValue).toString();
                        this.objUser.location = currentUser.claims.filter(element => element.claimType === 'Location').map(element => element.claimValue).toString();
                        this.showNewUser=true;
                    }
                })
            },
            async activateDeactivateUser() {
                const promise = axios({
                    url: '/User/ActivateOrDeactivateUser?userId=' + this.userId + '&isActive=' + this.userStatus + '&orgName=' + this.currentOrgName + '',
                    method: 'post',
                    baseURL: process.env.VUE_APP_UmApisBaseUrl,
                })
                promise.then(response => {
                    if (response.status == 200)
                        this.gridRefresh();                        
                })
                this.$root.$emit('bv::hide::modal', 'deactiveUser');                        
            },
            cancel() {
                this.objUser = this.assignUserFields();
                this.showNewUser=false;this.userError=false;
            },
            getactiveDeactiveStatus(id, status){
                this.userId = id;
                this.userStatus = status;
                 this.$root.$emit('bv::show::modal', 'deactiveUser');
            }
        }
    }
</script>